// Global style write by less
// Build to dist/app.wxss
//小箭头
.arrow {
  position: relative;
}
.arrow::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 40rpx;
  height: 26rpx;
  width: 26rpx;
  border: 2rpx solid #999;
  border-color: #999 #999 transparent transparent ;
  transform: translate(0, -50%) rotate(45deg);
}
// 公共样式
.new {
  border-top: 1rpx solid #ccc;
  padding: 20rpx 24rpx;
