  .up-photo {
    padding-bottom: 20rpx;
    border-bottom: 1rpx solid #ccc;
    margin-bottom: 20rpx;
    .img {
      width: 158rpx;
      height: 158rpx;
      margin-right: 16rpx;
    }
    .photo-wrap {
