  border-top: 8rpx solid #eee;
}

.text-wrap {
  display: flex;
  font-size: 30rpx;
  color: #333;
  font-weight: 700;
  align-items: center;
  border-bottom: 1rpx solid #eee;
  .name-l {
    line-height: 88rpx;
    font-size: 30rpx;
  }
  .name-r {
    flex: 1;
    line-height: 88rpx;
    //height: 88rpx;
    text-align: right;
    color: #999 !important;
    input {
      color: #999 !important;
      font-size: 30rpx;
    }
    .picker {
      color: #999 !important;
    }
    //font-size: 30rpx;
  }
  .address {
    line-height: 40rpx;
    text-align: right;
  }
  .addressName {
    transform: translateY(-25%);
  }
  //性别选择
  .gender {
    display: flex;
    align-items: center;
    padding-left: 300rpx;
    .choose-wrap {
      display: flex;
      align-items: center;
      margin-left: 42rpx;
    }
    .choose-box {
      width: 36rpx;
      height: 36rpx;
      border: 1rpx solid #ffc4a6;
      border-radius: 50%;
      margin-right: 10rpx;
    }
    .choose-text {
      font-size: 30rpx;
    }
    .choose-text::after {
      color: #999;
      font-size: 26rpx;
      margin-left: 10rpx;
    }
    .cur {
      position: relative;
    }
    .cur::after {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50.1%, -50.1%);
      content: '';
      width: 24rpx;
      height: 24rpx;
      background-color: #ffc4a6;
      border-radius: 50%;
