.invited {
  .name-r {
    color: #333 !important;
  }
}
.confirm {
  display: flex;
  view {
    width: 50%;
    box-sizing: border-box;
    border-top: 1rpx solid #ccc;
    line-height: 98rpx;
    text-align: center;
    font-size: 32rpx;
    color: #2b2f41;
    font-weight: 700;
  }
  view:nth-child(2) {
    border-top: 1rpx solid #2b2f41;
    background-color: #2b2f41;
    color: #ffc4a6;
  }
}
.mask, .cancelMask {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.4);
  font-size: 30rpx;
  .mask-wrap, .cancelMask-wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 560rpx;
    height: 295rpx;
    transform: translate(-50%, -50%);
    background: #fff;
    border-radius: 20rpx;
    text-align: center;
    .mask-title {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      padding: 40rpx 56rpx;
      box-sizing: border-box;
      line-height:42rpx;
      color: #000;
    }
    .mask-confirm {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      line-height: 99rpx;
      border-top: 1rpx solid #d2d3d5;
      font-size: 36rpx;
      color: #ffcea6;
    }
  }
}
.mask-picker {
  width: 100%;
  height: 400rpx;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #fff;
  view {
    text-align: center;
    color: #333;
  }
}
.placeholdercss {
  color: #ccc;
}
.moneyMask {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
